.newMessage textarea {
	width: 100%;
	height: 35px;
	padding: 5px 10px;
	border: none;
	border-radius: 5px;
	background-color: #ffffff;
	box-sizing: border-box;
	resize: none;
}

.newMessage button {
	width: 100%;
	height: 35px;
	margin-top: 10px;
	border: none;
	border-radius: 5px;
	background-color: #50b5ff;
	color: #ffffff;
	cursor: pointer;
}